import React, { Suspense, lazy } from "react"
import './_app'
import Header from "../01_header/_header"
import Calc from "../02_calc/_calc"
import BestOffers from "../03_bestOffers/_bestOffers"
import HowGet from "../04_howGet/_howGet"
import WaysToGet from "../05_waysToGet/_waysToGet"
import AboutService from "../08_AboutService/_AboutService"

const Reviews = lazy(() => import("../06_reviews/_reviews"))
const OurClients = lazy(() => import("../07_ourClients/_ourClients"))

export default function App() {
  return (
    <>
    <Header />
    <Calc />
    <BestOffers />
    <HowGet />
    <WaysToGet />
    <Suspense fallback={<div>Загрузка...</div>} >
      <Reviews />
      <OurClients />
    </Suspense>
    <AboutService />
    </>
  )
}